import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Box from 'common/src/components/Box';
import Text from 'common/src/components/Text';
import Image from 'common/src/components/Image';
import Heading from 'common/src/components/Heading';
import Button from 'common/src/components/Button';
import FeatureBlock from 'common/src/components/FeatureBlock';
import Container from 'common/src/components/UI/Container';
import Particles from '../Particle';
import BannerWrapper, {
  DiscountLabel,
  BannerObject,
} from './bannerSection.style';

import BannerObject1 from 'common/src/assets/image/saas/banner/bannerObject1.png';
import BannerObject2 from 'common/src/assets/image/saas/banner/screendesign_dashboard.png';

const BannerSection = ({
  row,
  col,
  colFull,
  colImage,
  title,
  title2,
  btnStyle,
  description,
  discountText,
  discountAmount,
  outlineBtnStyle,
  titleSubTitle
}) => {
  const ButtonGroup = () => (
    <Fragment>
      {/* <Button title="FREE TRAIL" {...btnStyle} /> */}
      <Button
        className="outlined"
        title="EXPLORE MORE"
        // variant="outlined"
        {...btnStyle}
        onClick={() => (window.location.href = '#service_section')}
      />
    </Fragment>
  );

  const NewBanner = (
    <p
      style={{
        fontSize: '12px',
        color: '#343d48cc',
        display: 'flex',
      }}
    >
      <b>**NEW </b>
    </p>
  );

  return (
    <>
      <BannerWrapper id="banner_section" style={{ marginTop: 60 }}>
        {/* <Particles /> */}
        <Container fullWidth>
            <Box className="col" {...col}>
              <FeatureBlock
                title={
                  <>
                    <Heading
                      content="Bespoke SaaS Tailored to your Organisation's Needs"
                      {...title}
                    />
                    <Text
                      content="Forget must-haves, should-haves, and nice-to-haves. Software should adapt to how your organisation works, not the other way around."
                      {...description}
                    />
                    <Text
                      content="Rapydly checks off everything on your wishlist."
                      {...description}
                    />
                  </>
                }
                // button={<ButtonGroup />}
              />
            </Box>
            <Box className="col" {...colImage}>
              <BannerObject>
                <Image
                  src={BannerObject2}
                  alt="BannerObject1"
                />
              </BannerObject>
            </Box>
        </Container>
      </BannerWrapper>

      <BannerWrapper id="service_section">
        <Container>
          <Box className="row" {...row}>
            <Box className="col" {...colFull}>
              {/* <DiscountLabel>
              <Text content="25% Discount" {...discountAmount} />
              <Text content="on every first project " {...discountText} />
            </DiscountLabel> */}
              <FeatureBlock
                title={
                  <>
                    {/* {NewBanner}  */}
                    <Heading
                      content={
                        <>
                          {"Changing the Game for Enterprise Software"}
                        </>
                      }
                      {...title2}
                      textAlign={'center'}
                    />
                  </>
                }
                description={
                  <>
                  <Text
                    content={
                      <>
                        {
                          'Bespoke software is custom, but it usually takes a lot of time and money.' 
                        }
                      </>
                    }
                    {...description}
                  />
                  <Text
                    content={
                      <>
                        {
                          'SaaS is convenient, but it usually doesn\'t do everything you\'d like it to.' 
                        }
                      </>
                    }
                    {...description}
                  />
                  <Text
                    content={
                      <>
                        {
                          'Rapydly has redefined how organisations introduce software into the workplace by providing bespoke software on a SaaS budget and timeline.' 
                        }
                      </>
                    }
                    {...description}
                  />
                  </>
                }
              />
            </Box>
          </Box>
        </Container>
      </BannerWrapper>
    </>
  );
};

BannerSection.propTypes = {
  title: PropTypes.object,
  btnStyle: PropTypes.object,
  description: PropTypes.object,
  contentStyle: PropTypes.object,
  discountText: PropTypes.object,
  discountAmount: PropTypes.object,
  outlineBtnStyle: PropTypes.object,
};

BannerSection.defaultProps = {
  row: {
    maxWidth: 1600,
    flexBox: true,
    flexWrap: 'wrap',
    ml: 'auto',
    mr: 'auto',
    alignItems: 'center',
  },
  col: {
    pr: '15px',
    pl: '15px',
    width: [1, 1, '600px'],
    margin: 'auto',
    mb: [ 28, 28, 52]
  },
  colImage: {
    pr: '15px',
    pl: '15px',
    width: [1, 1, 1],
  },
  colFull: {
    pr: '15px',
    pl: '15px',
    width: [1, 1, '80%'],
    margin: 'auto',
  },
  title: {
    fontSize: ['32px', '40px', '42px'],
    fontWeight: '600',
    color: '#0f2137',
    letterSpacing: '-0.025em',
    lineHeight: '1.5',
    textAlign: 'center',
    textTransform: 'capitalize'
  },
  titleSubTitle: {
    fontSize: ['16px', '16px', '16px'],
    color: '#343d48',
    textAlign: 'center',
    paddingLeft: [0, 0, 50],
    paddingRight: [0, 0, 50],
    lineHeight: '1.5',
    textAlign: 'center'
  },
  title2: {
    fontSize: ['29px', '32px', '35px'],
    fontWeight: '500',
    color: '#0f2137',
    letterSpacing: '-0.025em',
    lineHeight: '1.5',
    textAlign: ['left', 'center'],
  },
  description: {
    fontSize: '16px',
    color: '#343d48cc',
    lineHeight: '1.5',
    mb: '8px',
    textAlign: 'center',
    paddingLeft: [0, 0, 50],
    paddingRight: [0, 0, 50],
  },
  btnStyle: {
    minWidth: ['120px', '120px', '120px', '156px'],
    fontSize: ['13px', '14px'],
    fontWeight: '500',
    colors: 'primaryWithBg',
  },
  outlineBtnStyle: {
    minWidth: '156px',
    fontSize: '14px',
    fontWeight: '500',
    color: '#50b148',
    // ml: '18px',
  },
  discountAmount: {
    fontSize: '14px',
    color: '#50b148',
    mb: 0,
    as: 'span',
    mr: '0.4em',
    fontWeight: 700,
  },
  discountText: {
    fontSize: ['13px', '14px'],
    color: '#0f2137',
    mb: 0,
    as: 'span',
    fontWeight: 500,
  },
};

export default BannerSection;
