import React, { useState, Fragment } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Icon from 'react-icons-kit';
import Box from 'common/src/components/Box';
import Text from 'common/src/components/Text';
import Heading from 'common/src/components/Heading';
import Image from 'common/src/components/Image';
import Button from 'common/src/components/Button';
import Container from 'common/src/components/UI/Container';
import GlideCarousel from 'common/src/components/GlideCarousel';
import GlideSlide from 'common/src/components/GlideCarousel/glideSlide';

import PricingTable, {
  PricingHead,
  PricingPrice,
  PricingButton,
  PricingList,
  ListItem,
  PricingButtonWrapper,
} from './pricing.style';

import { checkmark } from 'react-icons-kit/icomoon/checkmark';

import Demo1 from 'common/src/assets/image/saas/banner/demo1.png';
import Demo2 from 'common/src/assets/image/saas/banner/demo2.png';
import Demo3 from 'common/src/assets/image/saas/banner/demo3.png';

const tabs = [
  {
    title:
      'View, search, filter, and traverse through a UI dynamically built from your database structure.',
    image: Demo1,
  },
  {
    title: 'Create, update, and delete data with the always up-to-date UI.',
    image: Demo2,
  },
  {
    title:
      'Fully customise the UI with preferences like display names, hiding/showing fields, and setting a color scheme.',
    image: Demo3,
  },
];

const WebUIForDatabase = ({
  sectionWrapper,
  row,
  secTitleWrapper,
  secHeading,
  secText,
  nameStyle,
  descriptionStyle,
  priceStyle,
  btnStyle,
  priceLabelStyle,
  buttonStyle,
  buttonFillStyle,
  listContentStyle,
  description,
}) => {
  const Data = useStaticQuery(graphql`
    query {
      saasJson {
        THE_PRODUCT {
          name
          price
          priceLabel
          description
          buttonLabel
          url
          freePlan
          listItems {
            content
          }
        }
        THE_SERVICE {
          name
          price
          priceLabel
          description
          buttonLabel
          url
          freePlan
          listItems {
            content
          }
        }
        BACKEND {
          name
          price
          priceLabel
          description
          buttonLabel
          url
          freePlan
          listItems {
            content
          }
        }
      }
    }
  `);

  const [state, setState] = useState({
    data: tabs,
    active: 1,
  });

  const data = state.data;
  const active = state.active;

  const pricingCarouselOptions = {
    type: 'slider',
    perView: 3,
    gap: 30,
    bound: true,
    breakpoints: {
      1199: {
        perView: 2,
        peek: {
          before: 100,
          after: 100,
        },
      },
      990: {
        perView: 1,
        peek: {
          before: 160,
          after: 160,
        },
      },
      767: {
        perView: 1,
        peek: {
          before: 80,
          after: 80,
        },
      },
      575: {
        perView: 1,
        gap: 15,
        peek: {
          before: 20,
          after: 20,
        },
      },
    },
  };

  return (
    <Box {...sectionWrapper} id="pricing_section">
      <Container>
        <Box {...secTitleWrapper}>
          <Text {...secText} />
          <Text {...description} />
          <PricingButtonWrapper>
            <Button
              title="View"
              className={active === 1 ? 'active-item' : ''}
              onClick={() =>
                setState({
                  data: tabs[0],
                  active: 1,
                })
              }
            />
            <Button
              title="Manage"
              className={active === 2 ? 'active-item' : ''}
              onClick={() =>
                setState({
                  data: tabs[1],
                  active: 2,
                })
              }
            />
            <Button
              title="Customise"
              className={active === 3 ? 'active-item' : ''}
              onClick={() =>
                setState({
                  data: tabs[2],
                  active: 3,
                })
              }
            />
          </PricingButtonWrapper>
        </Box>
        <Box {...row}>
          <Text {...description} content={data.title} />
          <Image src={data.image} style={{}} />
        </Box>
        <div style={{ width: '100%', textAlign: 'center', marginTop: 80 }}>
          <Button
            className="outlined"
            title="CONTACT US"
            // variant="outlined"
            {...btnStyle}
            onClick={() => (window.location.href = '#contact')}
          />
        </div>
      </Container>
    </Box>
  );
};

WebUIForDatabase.propTypes = {
  sectionWrapper: PropTypes.object,
  row: PropTypes.object,
  col: PropTypes.object,
  secTitleWrapper: PropTypes.object,
  secHeading: PropTypes.object,
  secText: PropTypes.object,
  nameStyle: PropTypes.object,
  descriptionStyle: PropTypes.object,
  priceStyle: PropTypes.object,
  priceLabelStyle: PropTypes.object,
  listContentStyle: PropTypes.object,
};

WebUIForDatabase.defaultProps = {
  sectionWrapper: {
    as: 'section',
    pt: ['60px', '80px', '80px', '80px', '120px'],
    pb: ['20px', '20px', '20px', '80px'],
  },
  description: {
    fontSize: '18px',
    color: '#343d48',
    maxWidth: '900px',
    mb: 50,
    margin: 'auto',
    content:
      'On-demand dynamic back office UIs, allowing anyone with access the ability to edit and view data, without going directly to the database.',
  },
  row: {
    flexBox: true,
    flexWrap: 'wrap',
    ml: '-15px',
    mr: '-15px',
    alignItems: 'center',
  },
  secTitleWrapper: {
    mb: ['50px'],
  },
  btnStyle: {
    minWidth: ['120px', '120px', '120px', '156px'],
    fontSize: ['13px', '14px'],
    fontWeight: '500',
    colors: 'primaryWithBg',
  },
  secText: {
    content: 'WEB UI FOR DATABASE',
    as: 'span',
    display: 'block',
    textAlign: 'center',
    fontSize: '14px',
    letterSpacing: '0.15em',
    fontWeight: '700',
    color: '#50b148',
    mb: '10px',
  },
  secHeading: {
    content: 'What different services and products do we offer',
    textAlign: 'center',
    fontSize: ['20px', '24px'],
    fontWeight: '500',
    color: 'headingColor',
    letterSpacing: '-0.025em',
    mb: '0',
  },
  col: {
    width: [1, 1 / 2, 1 / 2, 1 / 3],
    pr: '15px',
    pl: '15px',
  },
  nameStyle: {
    fontSize: ['20px', '20px', '22px', '22px', '22px'],
    fontWeight: '500',
    color: 'headingColor',
    letterSpacing: '-0.025em',
    textAlign: 'center',
    mb: '12px',
  },
  descriptionStyle: {
    fontSize: ['15px', '16px', '16px', '16px', '16px'],
    color: 'textColor',
    lineHeight: '1.75',
    textAlign: 'center',
    mb: '0',
  },
  priceStyle: {
    as: 'span',
    display: 'block',
    fontSize: ['36px', '36px', '40px', '40px', '40px'],
    color: 'headingColor',
    textAlign: 'center',
    mb: '5px',
    letterSpacing: '-0.025em',
  },
  priceLabelStyle: {
    fontSize: ['13px', '14px', '14px', '14px', '14px'],
    color: 'textColor',
    lineHeight: '1.75',
    textAlign: 'center',
    mb: '0',
  },
  buttonStyle: {
    type: 'button',
    fontSize: '14px',
    fontWeight: '600',
    borderRadius: '4px',
    pl: '10px',
    pr: '10px',
    colors: 'primary',
    width: '222px',
    maxWidth: '100%',
  },
  buttonFillStyle: {
    type: 'button',
    fontSize: '14px',
    fontWeight: '600',
    color: 'white',
    borderRadius: '4px',
    pl: '10px',
    pr: '10px',
    colors: 'primaryWithBg',
    width: '200px',
    maxWidth: '100%',
  },
  listContentStyle: {
    fontSize: ['15px', '16px', '16px', '16px', '16px'],
    color: 'textColor',
    mb: '0',
  },
};

export default WebUIForDatabase;
